import * as React from 'react'
import UIElement from "../interfaces/UIElement";

interface Store {
    content?: string
}

export default class Example
    extends React.Component<Store, Store>
    implements UIElement
{
    static defaultProps = {}

    state: Store = {  }

    handleClick = (e: any) => {
        this.setState({
            content: 'New Content'
        })
    }

    render() {
        return (
            <div onClick={this.handleClick}>
                {this.state.content}
            </div>
        )
    }
}
